*{
  margin: 0;
}

body{
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.App{
  height: 100vh;
  background-color: #dadbd3;
  display: grid;
  place-items: center;
}

.app__body{
  height: 90vh;
  width: 90vw;
  background-color: #ededed;
  box-shadow: -1px 4px 20px -6px #000;
  display: flex;
}